      align-items: center;
      gap: 10px;
      left: -145px;
      top: 160px;
      .span1 {
        color: #a7a7a9;
        font-size: 15px;
      }
    }
    --clip-path: xywh(
      calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--Height))
        calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--Height))
        round 20px 20px 20px 20px
    );
    animation: movewrapper8 5s infinite;
  }
  .wrapper9 {
    z-index: -10;
    .wrap9Text {
      position: relative;
      left: -65px;
      top: 80px;
      flex-direction: column;
      .span1 {
        font-size: 20px;
      }
      .span2 {
        color: #a7a7a9;
        font-size: 10px;
      }
    }
    --clip-path: xywh(
      calc(var(--calculated-value) / var(--gapW) * 2%) calc(var(--Height))
        calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--Height))
        round 20px 20px 20px 20px
    );
    animation: movewrapper9 5s infinite;
  }

  /* /////////////////////// */
  @keyframes movewrapper7 {
    0% {
      transform: translateY(270px);
    }
    20% {
      transform: translateY(-30px);
    }
    40% {
      transform: translateY(-30px) translateX(150px);
    }
    60% {
      transform: translateY(-30px) translateX(65px);
    }
    80% {
      transform: translateY(-30px) translateX(0px);
    }

    100% {
      transform: translateY(-30px) translateY(0px);
    }
  }
  @keyframes movewrapper7Deg {
    0% {
      transform: rotate(360deg);
    }
    20% {
      transform: rotate(360deg);
    }
    40% {
      transform: rotate(90deg);
    }
    60% {
      transform: rotate(360deg);
    }
    80% {
      transform: rotate(90deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes movewrapper8 {
    0% {
      transform: translateY(270px) translateX(-30px);
    }
    20% {
      transform: translateY(-75px) translateX(-30px);
    }
    40% {
      transform: translateY(-75px) translateX(150px);
    }
    60% {
      transform: translateY(-75px) translateX(45px);
    }
    80% {
      transform: translateY(-75px) translateX(0px);
    }

    100% {
      transform: translateY(-75px) translateX(-30px);
    }
  }
  @keyframes movewrapper9 {
    0% {
      transform: translateY(230px);
    }
    20% {
      transform: translateY(0px);
    }
    40% {
      transform: translateY(0px);
    }
    60% {
      transform: translateY(calc(var(--Height) * (-1)));
    }
    80% {
      transform: translateY(calc(var(--Height) * (-1)));
    }

    100% {
      transform: translateY(0px);
    }
  }

